import React, { PureComponent } from 'react'

import AutoSizer from 'react-virtualized/dist/commonjs/AutoSizer'
import List from 'react-virtualized/dist/commonjs/List'
import CellMeasurer from 'react-virtualized/dist/commonjs/CellMeasurer'

import { cache, refreshCache } from './cache'

import Button from 'react-bootstrap/Button'

import '../TableStyles.css'
import _debounce from 'lodash.debounce'

// import anchorme from 'anchorme'

import ChatLine from './chatLine'

import FormatDate from '../helper/FormatDate'
// import formatEmotes from '../helper/formatEmotes'
// import getRandomColor from '../helper/getRandomColor'
// import classifierNumber from '../helper/classifierNumber'
// import LevelColor from '../helper/colors'

import { connect } from 'react-redux'
import { update } from '../redux'

const debug = false

// Limitations and Performance Considerations // https://github.com/bvaughn/react-virtualized/blob/a41ff4857ea08c604db3a64f556ff08135cfbfcc/docs/CellMeasurer.md
// Performance
// Measuring a column's width requires measuring all rows in order to determine the widest occurrence of that column. The same is true in reverse for measuring 
// a row's height.For this reason it may not be a good idea to use this HOC for Grids containing a large number of both columns and cells.

// Since this component measures one cell at a time to determine it's width/height, it will likely be slow if a user skips many rows (or columns) at once 
// by scrolling with a scrollbar or via a scroll-to-cell prop. There is (unfortunately) no workaround for this performance limitation at the moment.

const getBrowserHiddenProps = () => {
  let hidden, visibilityChange
  if (typeof document.hidden !== 'undefined') {
    hidden = 'hidden'
    visibilityChange = 'visibilitychange'
  } else if (typeof document.msHidden !== 'undefined') {
    hidden = 'msHidden'
    visibilityChange = 'msvisibilitychange'
  } else if (typeof document.webkitHidden !== 'undefined') {
    hidden = 'webkitHidden'
    visibilityChange = 'webkitvisibilitychange'
  }

  return {
    hidden,
    visibilityChange
  }
}

class LogContainer extends PureComponent {
  constructor(props) {
    super(props)
    if (debug) console.log('LogContainer constructor', props)
    

    this.bindListRef = React.createRef()

    this.state = {
      mobile: window.innerWidth < 640,
      scrollBottom: true
    }

    this.updateFilter = this.updateFilter.bind(this)

    const { hidden, visibilityChange } = getBrowserHiddenProps() // https://dev.to/hurricaneinteractive/guilt-users-into-coming-back-with-your-document-title-55kn

    if (typeof document.addEventListener !== 'undefined' && typeof hidden !== 'undefined') {
      document.addEventListener(visibilityChange, () => {
        if (!document[hidden]) {
          refreshCache()
          // if (this.bindListRef.current) {
          //   this.bindListRef.current.forceUpdateGrid()
          // }
        }
      }, false)
    }
  }

  // bindListRef = ref => { this.list = ref } // eslint-disable-line

  componentDidMount() {
    if (debug) console.log('componentDidMount()')
    if (this.bindListRef.current) {
      this.bindListRef.current.forceUpdateGrid()
    }
    window.addEventListener('resize', _debounce(() => this.handleResize(window.innerWidth), 100))
  }

  componentWillUnmount() {
    try {
      window.removeResizeListener('resize', _debounce(() => this.handleResize(window.innerWidth), 100))
    } catch (error) { }
  }

  componentDidUpdate(prevProps, _prevState, _snapshot) { // https://stackoverflow.com/a/51770376
    if (debug) console.log('componentDidUpdate()')
    // if (debug) console.log(`${this.state.scrollBottom} && ${this.props.length} !== ${prevProps.length}`)

    if (this.props.length !== prevProps.length) {
      if (this.props.scrollBottom) { // if (this.state.scrollBottom) {
        const length = this.props.length
        this.bindListRef.current.scrollToRow(length)
        // if (debug) console.log(`1-this.bindListRef.current.scrollToRow(${length})`)
        this.setState({ _scrollingToRow: length }, () => {
          this.bindListRef.current.scrollToRow(length)
          // if (debug) console.log(`2-this.bindListRef.current.scrollToRow(${length})`)
        })
      }

      if (this.props.filter !== prevProps.filter) {
        refreshCache() // jedes mal wenn ein Filter ge√§ndert wird
        // f√ºr mehr performance br√§uchte man eine UUID
      }
    }
  }

  handleResize = (innerWidth) => {
    if (debug) console.log('handleResize()')
    if (innerWidth < 640) this.setState({ mobile: true })
    else this.setState({ mobile: false })

    refreshCache()
    if (this.bindListRef.current) {
      this.bindListRef.current.forceUpdateGrid()
    }
  }

  listenScrollEvent = ({ scrollHeight, scrollTop, clientHeight }) => {
    if (debug) console.log(`listenScrollEvent() if ((${scrollHeight} - ${scrollTop}) === ${clientHeight})`)
    if (scrollHeight === 0 && scrollTop === 0) return

    if ((scrollHeight - scrollTop) === clientHeight) {
      if (!this.props.scrollBottom) this.props.update(true, 'scrollBottom')
    } else {
      if (this.props.scrollBottom) this.props.update(false, 'scrollBottom')
    }
  }

  updateFilter(userInput, type) {
    if (debug) console.log('updateFilter()', userInput, type)
    if (this.props.filter[type] === userInput) userInput = ''
    this.props.update(userInput, 'ADD_FILTER_' + type)
  }

  classifierLevel(row) {
    // const { result, classcolor } = helper.classifierLevel(cellData)
    return <span onClick={() => this.updateFilter(row.chan, 'channel')}>{row.chan}</span>
  }

  UserNameRenderer = (row) => {
    return <span className='username' onClick={() => this.updateFilter(row.name, 'name')} style={{ color: (row && row.tags && row.tags.color ? row.tags.color : '#EEE') }}>{row.name}</span>
  }

  decamelize = (text) => {
    return text
      .replace(/([a-z\d])([A-Z])/g, '$1-$2')
      .replace(/([A-Z]+)([A-Z][a-z\d]+)/g, '$1-$2')
      .toLowerCase()
  }

  createBadge = ({ measure, name, badge, tags }) => {
    // console.log('1createBadge', name, badge, tags)
    switch (name) {
      case 'subscriber':
        if (badge.versions[tags.badges.subscriber]) {
          badge = badge.versions[tags.badges.subscriber]
        } else {
          badge = Object.keys(badge.versions)[0]
        }
        break

      case 'bits':
        if (badge.versions[tags.badges.bits]) {
          badge = badge.versions[tags.badges.bits]
        } else {
          badge = Object.keys(badge.versions)[0]
        }
        break

      default:
        if (badge.versions['0']) badge = badge.versions['0']
        else if (badge.versions['1']) badge = badge.versions['1']
        else badge = Object.keys(badge.versions)[0]
    }
    // console.log('2createBadge', badge)
    
    try {
      return <img
        onLoad={measure}
        key={name || ''}
        title={badge.title || ''}
        alt={badge.title || ''}
        aria-label={badge.title || ''}
        className='line'
        src={badge.image_url_1x}
        srcSet={`${badge.image_url_1x} 1x, ${badge.image_url_2x} 2x, ${badge.image_url_4x} 4x`}
      ></img>
    } catch (error) {
      console.warn(error)
      return <img
        onLoad={measure}
        key={name}
        alt={name}
        className='line'
      ></img>
    }
  }

  createBadgeTags = ({ source, measure }) => {
    let badges = []
    if (source && source.tags && source.tags.badges) {

      for (let name in source.tags.badges) {
        if (source.tags.badges.hasOwnProperty(name)) {
          let element

          // https://discuss.dev.twitch.tv/t/how-to-get-emotes-badges-object/18916/2
          if (this.props.badges[source.chan] && this.props.badges[source.chan].has(name)) {
            // console.log(`this.props.badges[${source.chan}].has(${name})`)
            const badge = this.props.badges[source.chan].get(name)
            element = this.createBadge({ measure, name, badge, tags: source.tags })
          } else if (this.props.badges.global.has(name)) {
            // console.log(`this.props.badges.global.has(${name})`)
            const badge = this.props.badges.global.get(name)
            element = this.createBadge({ measure, name, badge, tags: source.tags })
          } else {
            element = <span className='line badge badge-primary' key={name} title={name}>{name}</span>
          }
          badges.push(element)
        }
      }
    }
    
    return badges
  }

  render() {
    if (debug) console.log('LogContainer RENDER', this.props, '############################################')

    let scrollButton = ''
    if (!this.props.scrollBottom) { // TODO: nur wenn man auch scrollen kann
      scrollButton = <Button variant='secondary' id='scrollDown' onClick={() => {
        this.bindListRef.current.scrollToRow(this.props.length)
        this.props.update(true, 'scrollBottom')
        refreshCache()
      }}>scroll down</Button>
    }

    return (
      <>
        <div style={{
          height: 'calc(100vh - 60px)',
          fontSize: Number(this.props.settings.get('fontSize')),
          fontFamily: `'${this.props.settings.get('fontClass')}', monospace`
        }} className={'bg-dark text-list'}>

          <AutoSizer>
            {({ height, width }) => (

              <List
                id='list'
                // height={window.innerHeight - 60}
                // width={window.innerWidth - 3}
                height={height}
                width={width - 3}
                overscanColumnCount={0}
                overscanRowCount={10}
                scrollingResetTimeInterval={0}
                ref={this.bindListRef}
                rowCount={this.props.length}
                deferredMeasurementCache={cache}
                onScroll={this.listenScrollEvent}
                rowHeight={cache.rowHeight}
                noRowsRenderer={() => <div><center><i>Nichts gefunden</i></center></div>}
                rowRenderer={({ // https://github.com/bvaughn/react-virtualized/blob/8738097a6ead422f74590d6a631bd5fd83ebc831/docs/List.md#rowrenderer
                  index,       // Index of row
                  // isScrolling, // The List is currently being scrolled
                  // isVisible,   // This row is visible within the List (eg it is not an overscanned row)
                  _key,         // Unique key within array of rendered rows
                  parent,      // Reference to the parent List (instance)
                  style        // Style object to be applied to row (to position it);
                  // This must be passed through to the rendered row element.
                }) => {
                  let key
                  const source = this.props.logs.get(index) // This comes from your list data
                  if (source.tags && source.tags.id) {
                    key = source.tags.id
                  } else {
                    key = _key || index
                  }

                  const channelStyle = this.props.settings.get('channelColor') && this.props.settings.get('channelColor')[source.chan] ? { backgroundColor: this.props.settings.get('channelColor')[source.chan] } : {}
                  const channel = <><span className='line badge badge-primary' style={channelStyle} onClick={() => this.updateFilter(source.chan, 'channel')}>{source.chan}</span></>

                  return (
                    <CellMeasurer // https://github.com/bvaughn/react-virtualized/blob/master/docs/CellMeasurer.md
                      cache={cache}
                      columnIndex={0}
                      key={key}
                      parent={parent}
                      rowIndex={index}
                    >
                      {({ measure }) => (

                        <div
                          key={key}
                          style={{
                            ...style,
                            padding: '.3rem', // '.5rem 2rem',
                            wordWrap: 'break-word',
                            lineHeight: '20px'
                          }}
                        >
                          <span>{FormatDate(source.timestamp, this.state.mobile)}</span>:
                          {channel}
                          {(this.props.settings.get('twitchEmotes') ? this.createBadgeTags({ source, measure }) : '')} {/* // TODO: twitchBadges w√§re richtig */}
                          {this.UserNameRenderer(source)}
                          <ChatLine
                            source={source}
                            emotes={this.props.emotes}
                            twitchEmotes={this.props.settings.get('twitchEmotes')}
                            bttvEmotes={this.props.settings.get('bttvEmotes')}
                            ffzEmotes={this.props.settings.get('ffzEmotes')}
                            measure={measure}
                          />
                        </div>
                      )}
                    </CellMeasurer>
                  )

                }}
              />

            )}
          </AutoSizer>

          {scrollButton}
        </div>
      </>
    )
  }
}

export default connect(
  state => ({
    scrollBottom: state.scrollBottom,
    logs: state.logs,
    length: state.length,
    filter: state.filter,
    settings: state.settings,
    badges: state.badges,
    emotes: state.emotes
  }), // mapStateToProps
  { update } // mapDispatchToProps
)(LogContainer)
