import React from 'react'

const splitWords = RegExp(/\w+/, 'g')

export default ({ msg, emotes, emotesArray, measure, id, twitchEmotes, bttvEmotes, ffzEmotes, chan }) => {
  const bttvObj = []
  const ffzObj = []
  if (bttvEmotes || ffzEmotes) {
    let word
    while ((word = splitWords.exec(msg)) !== null) {
      if (bttvEmotes) {
        if (emotes && emotes.globalbttv && emotes.globalbttv.has(word[0])) {
          bttvObj.push({
            badge: emotes.globalbttv.get(word[0]),
            name: word[0],
            start: word.index,
            end: word.index + word[0].length
          })
        } else if (emotes && emotes[`${chan}-bttv`] && emotes[`${chan}-bttv`].has(word[0])) {
          bttvObj.push({
            badge: emotes[`${chan}-bttv`].get(word[0]),
            name: word[0],
            start: word.index,
            end: word.index + word[0].length
          })
        }
      }

      if (ffzEmotes) {
        if (emotes && emotes.globalffz && emotes.globalffz.has(word[0])) {
          ffzObj.push({
            badge: emotes.globalffz.get(word[0]),
            name: word[0],
            start: word.index,
            end: word.index + word[0].length
          })
        } else if (emotes && emotes[`${chan}-ffz`] && emotes[`${chan}-ffz`].has(word[0])) {
          ffzObj.push({
            badge: emotes[`${chan}-ffz`].get(word[0]),
            name: word[0],
            start: word.index,
            end: word.index + word[0].length
          })
        }
      }
    }
  }

  // if (!(emotes.length || bttvObj.length || ffzObj.length)) {
  //   return msg
  // }

  if (emotesArray.length === 0 && bttvObj.length === 0 && ffzObj.length === 0) {
    return msg
  }

  // if (emotes.length === 0 && bttvObj.length === 0) {
  //   return msg
  // }

  // if (emotes.length === 0) {
  //   return msg
  // }

  // msg = msgBttvEmote
  // console.log(msgBttvEmote)

  // msg = emotes.map((emote, i) => {
  //   const img = <img
  //     key={`${id}-${emote.id}-${i}`}
  //     // onLoad={measure}
  //     className='emoticon'
  //     alt={emote.id}
  //     src={'//static-cdn.jtvnw.net/emoticons/v1/' + emote.id + '/1.0'}
  //     srcSet={`//static-cdn.jtvnw.net/emoticons/v1/${emote.id}/2 2x, //static-cdn.jtvnw.net/emoticons/v1/${emote.id}/4 4x`}
  //   />
  //   const start = emote.start
  //   const ende = emote.end + 1
  //   const left = msg.slice(0, start)
  //   const right = msg.slice(ende)
  //   return (<span key={`${emote.id}-${i}`}>{left.concat(img, right)}</span>)
  // })
  // chatLine.js: 25 emotes: 0.002685546875ms
  // chatLine.js: 25 emotes: 0.00390625ms
  // chatLine.js: 25 emotes: 0.004150390625ms
  // chatLine.js: 25 emotes: 0.003173828125ms
  // chatLine.js: 25 emotes: 0.057861328125ms
  // chatLine.js: 25 emotes: 0.004150390625ms
  // chatLine.js: 25 emotes: 0.0029296875ms
  // chatLine.js: 25 emotes: 0.003173828125ms
  // chatLine.js: 25 emotes: 0.03515625ms

  // // // // // // // // // // // // // // // // // //

  // for (let i = emotes.length; i--;) {
  //   msg.splice(
  //     emotes[i].start,
  //     emotes[i].end + 1,
  //     <img
  //       key={`${id}-${emotes[i].id}-${i}`}
  //       // onLoad={measure}
  //       className='emoticon'
  //       alt={emotes[i].id}
  //       src={'//static-cdn.jtvnw.net/emoticons/v1/' + emotes[i].id + '/1.0'}
  //       srcSet={`//static-cdn.jtvnw.net/emoticons/v1/${emotes[i].id}/2 2x, //static-cdn.jtvnw.net/emoticons/v1/${emotes[i].id}/4 4x`}
  //     />
  //   )
  // }
  // chatLine.js: 25 emotes: 0.003173828125ms
  // chatLine.js: 25 emotes: 0.027099609375ms
  // chatLine.js: 25 emotes: 0.00390625ms
  // chatLine.js: 25 emotes: 0.004150390625ms
  // chatLine.js: 25 emotes: 0.00390625ms
  // chatLine.js: 25 emotes: 0.0029296875ms
  // chatLine.js: 25 emotes: 0.003173828125ms
  // chatLine.js: 25 emotes: 0.002685546875ms
  // chatLine.js: 25 emotes: 0.004150390625ms
  // chatLine.js: 25 emotes: 0.029052734375ms
  // chatLine.js: 25 emotes: 0.0029296875ms

  // // // // // // // // // // // // // // // // // //

  msg = msg.split('') // TODO problem f√ºr BTTV und FTZ

  for (const i in emotesArray) { // https://github.com/tmijs/tmi.js/issues/11
    const e = emotesArray[i]
    const length = e.end - e.start
    const empty = Array.apply(null, new Array(length + 1)).map(function () { return '' })
    msg = msg.slice(0, e.start).concat(empty).concat(msg.slice(e.end + 1, msg.length))
    msg.splice(e.start, 1, <img
      key={`${id}-${e.id}-${i}`}
      onLoad={measure}
      className='emoticon'
      title={'Twitch Emoticon'}
      alt={e.id}
      src={`//static-cdn.jtvnw.net/emoticons/v1/${e.id}/1.0`}
      srcSet={`//static-cdn.jtvnw.net/emoticons/v1/${e.id}/2.0 2x, //static-cdn.jtvnw.net/emoticons/v1/${e.id}/4.0 4x`}
    />)
  }

  if (bttvEmotes) {
    for (const i in bttvObj) { // https://github.com/tmijs/tmi.js/issues/11
      const e = bttvObj[i]
      const length = e.end - e.start
      const empty = Array.apply(null, new Array(length + 1)).map(function () { return '' })
      msg = msg.slice(0, e.start).concat(empty).concat(msg.slice(e.end + 1, msg.length))
      msg.splice(e.start, 1,
        <img
          key={`${e.badge.code}-${e.badge.id}-${i}`}
          onLoad={measure}
          className='emoticon'
          title={'BTTV: ' + e.name}
          alt={e.badge.code}
          src={`//cdn.betterttv.net/emote/${e.badge.id}/1x`}
          srcSet={`//cdn.betterttv.net/emote/${e.badge.id}/2x 2x, //cdn.betterttv.net/emote/${e.badge.id}/3x 3x`}
        />)
    }
  }

  if (ffzEmotes) {
    for (const i in ffzObj) { // https://github.com/tmijs/tmi.js/issues/11
      const e = ffzObj[i]
      const length = e.end - e.start
      const empty = Array.apply(null, new Array(length + 1)).map(function () { return '' })
      msg = msg.slice(0, e.start).concat(empty).concat(msg.slice(e.end + 1, msg.length))
      msg.splice(e.start, 1,
        <img
          key={`${e.badge.name}-${e.badge.id}-${i}`}
          onLoad={measure}
          className='emoticon'
          title={'FFZ: ' + e.badge.name}
          alt={e.badge.name}
          src={`${e.badge.urls[1]}`}
          srcSet={`${e.badge.urls[2]} 2x, ${e.badge.urls[4]} 4x`}
        />)
    }
  }
  // chatLine.js: 25 emotes: 0.003173828125ms
  // chatLine.js: 25 emotes: 0.051025390625ms
  // chatLine.js: 25 emotes: 0.003173828125ms
  // chatLine.js: 25 emotes: 0.049072265625ms
  // chatLine.js: 25 emotes: 0.004150390625ms
  // chatLine.js: 25 emotes: 0.071044921875ms
  // chatLine.js: 25 emotes: 0.004150390625ms
  // chatLine.js: 25 emotes: 0.054931640625ms
  // chatLine.js: 25 emotes: 0.002685546875ms
  // chatLine.js: 25 emotes: 0.004150390625ms

  return msg
}
