const { chat } = new TwitchJs({ token: '', username: `justinfan${Math.floor((Math.random() * 80000) + 1000)}`, chat: { joinTimeout: 60000 }, log: { level: 0 } })

// // eslint-disable-next-line no-control-regex
// const ACTION = /(\x01ACTION )(.*)(\x01)/

export const _disconnect = () => {
  chat.disconnect()
}

const connected = () => chat._readyState === 3

export const _connect = ({ update, channels }) => {
  // console.log('_readyState', chat._readyState)
  // console.log(channels.toArray())
  if (connected()) { // https://github.com/twitch-devs/twitch-js/blob/083d8626c8fdfb19ff147a59f60d487b712be06c/src/Chat/index.js
    const StoreChannel = channels.toArray().map(chan => chan.name)
    const ChannelState = Object.keys(chat._channelState).map(chan => chan.substr(1))

    const _joins = difference(StoreChannel, ChannelState)
    if (_joins.length) {
      for (const _join of _joins) {
        chat.join(_join).then(data => {
          update({ name: _join, roomId: data.roomState.roomId }, 'ADD_ROOMID')
          update([{ timestamp: +new Date(), chan: _join, msg: '=> JOIN' }], 'ADD_DATA')
        })
      }
    }

    const _parts = difference(ChannelState, StoreChannel)
    if (_parts.length) {
      // console.log('part', _parts)
      for (const _part of _parts) {
        chat.join(_part).then(() => {
          update([{ timestamp: +new Date(), chan: _part, msg: '<= LEAVE' }], 'ADD_DATA')
        })
      }
    }
  } else if (chat._readyState === 0) {
    chat.connect()
      .then(async () => {
        // console.log('Connect ...')
        // await Promise.all(channels.toArray().map(channel => chat.join(channel.name))).then(data => console.log(data))
        channels.toArray().map(channel => chat.join(channel.name).then(_ => update([{ timestamp: +new Date(), chan: channel.name, msg: '=> JOIN' }], 'ADD_DATA')))
      })
      .catch(err => {
        console.warn(err)
      })

    chat.on('*', ({ channel, command, message, tags, timestamp, username, _raw }) => {
      timestamp = timestamp || +new Date()
      const chan = channel && channel.substr(1)
      const name = username
      // let msg
      switch (command) {
        case 'PRIVMSG':
          // console.log(_raw)
          // msg = formatEmotes(message, tags.emotes)
          // msg = message
          // if (tags.color === null) {
          //   tags.color = getRandomColor()
          // }
          // if (msg.toLowerCase().indexOf('@' + chan) !== -1) {
          //   tags.attention = true
          // }
          // const found = ACTION.exec(msg)
          // if (found !== null) {
          //   msg = found[2]
          //   tags.action = true
          // }

          if (tags && tags.badges) {
            if (tags.badges !== {}) {
              // delete tags.badges
              tags.badges_ = tags.badges
              tags.badges = parseBadges(_raw)
            }
          }

          update([{ timestamp, chan, name, msg: message, tags, raw: _raw }], 'ADD_DATA')
          break

        case 'USERNOTICE':
          update([{ timestamp, chan, msg: tags.systemMsg }], 'ADD_DATA')
          break

        case 'CLEARMSG': // https://dev.twitch.tv/docs/irc/tags#clearmsg-twitch-tags
          // Single message removal on a channel. This is triggered via /delete <target-msg-id> on IRC.
          // console.log('CLEARMSG', { channel, msg, tags, timestamp, username })
          tags = {
            ...tags,
            banReason: true,
            banMsg: '[CLEARMSG]'
          }
          // update([{ timestamp, chan, msg: `REMOVE ${username}, ${tags.targetMsgId}` }], 'ADD_DATA')
          update({ timestamp, chan, username, tags }, 'REMOVE_DATA')
          break

        case 'CLEARCHAT': // https://dev.twitch.tv/docs/irc/tags#clearchat-twitch-tags
          // Temporary or permanent ban on a channel.
          if (tags.banDuration) { // ban-duration (Optional) Duration of the timeout, in seconds. If omitted, the ban is permanent.
            tags.banMsg = `[TIMEOUT, ${tags.banDuration + 's' + (tags.banReason ? ', ' + tags.banReason : '')}]`
          } else {
            tags.banMsg = `[BAN, banReason: ${tags.banReason}]`
          }
          // update([{ timestamp, chan, username: 'twitch.tv', msg: tags.banMsg }], 'ADD_DATA')
          update({ timestamp, chan, username, tags }, 'EDIT_DATA')
          break

        case 'JOIN':
        //   update([{ timestamp, chan: '-', msg: '=> JOIN' }], 'ADD_DATA')
          break

        case 'PART':
        //   update([{ timestamp, chan: '-', msg: '<= PART' }], 'ADD_DATA')
          break

        case 'CONNECTED':
          update([{ timestamp, chan: 'twitch.tv', msg: 'CONNECTED' }], 'ADD_DATA')
          break

        case 'NOT_READY':
          update([{ timestamp, chan: 'twitch.tv', msg: 'TODO: NOT_READY' }], 'ADD_DATA')
          break

        case 'CONNECTING':
          update([{ timestamp, chan: 'twitch.tv', msg: 'TODO: CONNECTING' }], 'ADD_DATA')
          break

        case 'RECONNECTING':
          update([{ timestamp, chan: 'twitch.tv', msg: 'TODO: RECONNECTING' }], 'ADD_DATA')
          break

        case 'DISCONNECTING':
          update([{ timestamp, chan: 'twitch.tv', msg: 'TODO: DISCONNECTING' }], 'ADD_DATA')
          break

        case 'DISCONNECTED':
          update([{ timestamp, chan: 'twitch.tv', msg: 'TODO: DISCONNECTED' }], 'ADD_DATA')
          break

          // "HOSTTARGET"
          // { channel: "#tsm_viss", command: "HOSTTARGET", message: undefined, tags: { ‚Ä¶ }, timestamp: Sat Nov 02 2019 00: 00: 23 GMT + 0100(Mitteleurop√§ische Normalzeit), ‚Ä¶ }
          // channel: "#tsm_viss"
          // command: "HOSTTARGET"
          // message: undefined
          // tags: { }
          // timestamp: Sat Nov 02 2019 00: 00: 23 GMT + 0100(Mitteleurop√§ische Normalzeit) { }
          // username: "halifax"

        case 'NOTICE':
          update([{ timestamp, chan: channel, msg: message }], 'ADD_DATA')
          // { channel: "#tsm_viss", command: "NOTICE", message: "Now hosting Halifax.", tags: { ‚Ä¶ }, timestamp: Sat Nov 02 2019 00: 00: 23 GMT + 0100(Mitteleurop√§ische Normalzeit), ‚Ä¶ }
          // channel: "#tsm_viss"
          // command: "NOTICE"
          // message: "Now hosting Halifax."
          // tags: { msgId: "host_on" }
          // timestamp: Sat Nov 02 2019 00: 00: 23 GMT + 0100(Mitteleurop√§ische Normalzeit) { }
          // username: "tmi.twitch.tv"

          // { channel: "#eosandy", command: "NOTICE", message: "Now hosting Shurjoka.", tags: { ‚Ä¶ }, timestamp: Tue Nov 05 2019 17: 15: 04 GMT + 0100(Mitteleurop√§ische Normalzeit), ‚Ä¶ }
          // channel: "#eosandy"
          // command: "NOTICE"
          // message: "Now hosting Shurjoka."
          // tags: { msgId: "host_on" }
          // timestamp: Tue Nov 05 2019 17: 15: 04 GMT + 0100(Mitteleurop√§ische Normalzeit) { }
          // username: "tmi.twitch.tv"
          // __proto__: Object
          console.log('NOTICE', { channel, command, message, tags, timestamp, username })
          break

        case '001': ; break
        case '002': ; break
        case '003': ; break
        case '004': ; break
        case '353': ; break
        case '366': ; break
        case '372': ; break
        case '375': ; break
        case '376': ; break
        case 'CAP': ; break
        case 'MODE': ; break
        case 'PING': ; break
        case 'ROOMSTATE': ; break

        case 'PARSE_ERROR_ENCOUNTERED': ; break

        default:
          console.log('default', { channel, command, message, tags, timestamp, username })
      }
    })
  }

  return null
}

export default connect(
  state => ({
    channels: state.channels
  }), // mapStateToProps
  // state => ({ settings: state.settings }), // mapStateToProps
  // state => ({ }), // mapStateToProps
  { update } // mapDispatchToProps
)(_connect)
