import { createStore } from 'redux'
import { List, Map } from 'immutable' // https://immutable-js.github.io/immutable-js/
import Filter from './filter'
import { refreshCache } from '../components/cache'

import getRandomColor from '../helper/getRandomColor'

// import parseBadges from '../helper/parseBadges'

const debug = false
const timer = false

export const update = function (value, type) {
  if (debug) console.log('update()', { type, value })
  return ({ type, value })
}

