// https://github.com/koba04/redux-store-structure-patterns/blob/master/patterns/separated-list-store.ts?source=post_page---------------------------#L44
const LogReducer = (state = {}, action) => {
  // console.log(action)

  if (debug) console.log('Reducer action', action)
  if (debug) console.log('Reducer OLDStore', state)

  if (timer) console.time(action.type)
  switch (action.type) { // Logs
    case 'scrollBottom':
      if (timer) console.timeEnd(action.type)
      state = {
        ...state,
        scrollBottom: action.value
      }
      break
    case 'ADD_DATA':
      let ChannelColorList = state.settings.get('channelColor') || {}
      // console.log(action.value)

      for (let i = 0, len = action.value.length; i < len; i++) {
        if (!ChannelColorList[action.value[i].chan]) ChannelColorList[action.value[i].chan] = getRandomColor()

        // if (action.value[i] && action.value[i].tags && action.value[i].tags.badges) {
        //   if (action.value[i].tags.badges !== {}) {
        //     delete action.value[i].tags.badges
        //     action.value[i].tags.badges = parseBadges(action.value[i].raw)
        //   }
        // }
      }

      // List
      // const allLogs = state.allLogs.concat(action.value) // 2.008056640625ms
      const allLogs = state.allLogs.push(...action.value) // 1.783935546875ms

      // Seq
      // const allLogs = state.allLogs.concat(action.value)

      state.allLogs = allLogs
      break

    case 'REMOVE_DATA': // Es werden alle gefundenen ge√§ndert
      // console.log('action.value', action.value)
      // console.time('REMOVE_DATA')
      const found_ = state.allLogs.findIndex(line => {
        if (line && line.tags && line.tags.id && line.tags.id === action.value.tags.targetMsgId) {
          return true
        }
        return false
      })

      if (found_ !== -1) {
        const temp = state.allLogs.get(found_)
        state.allLogs = state.allLogs.set(found_, Object.assign({}, temp, { tags: action.value.tags }))
        console.log('REMOVE_DATA found_', found_, state.allLogs.get(found_))
      }

      // console.timeEnd('REMOVE_DATA')
      break

    case 'EDIT_DATA': // Es wird nur ein Eintrag ge√§ndert
      // console.time('EDIT_DATA')
      state.allLogs.forEach((item, index) => {
        let i = -1
        if (item.chan === action.value.chan && item.name === action.value.username) {
          i = index
        }
        if (i !== -1) {
          const temp = state.allLogs.get(i)
          state.allLogs = state.allLogs.set(i, Object.assign({}, temp, { tags: action.value.tags }))
        }
        return true
      })
      // console.timeEnd('EDIT_DATA')
      break

    case 'ADD_ROOMID':
      const found = state.channels.findIndex(ele => ele.name === action.value.name)
      if (found !== -1) {
        state.channels = state.channels.set(found, action.value)
        action.value = state.channels
      }
      localStorage.setItem('channels', JSON.stringify(action.value.toArray()))
      return {
        ...state,
        channels: action.value
      }
    case 'ADD_CHANNEL': // eslint-disable-line
      // TODO: pr√ºfe obs diesen Channel schon gibt
      ChannelColorList = state.settings.get('channelColor') || {}
      if (!(ChannelColorList && ChannelColorList[action.value.name])) ChannelColorList[action.value.name] = getRandomColor()
      const channels = state.channels.push({ name: action.value.name, roomId: action.value.roomId }) /* roomId is undefined */
      // localStorage.setItem('channels', JSON.stringify(action.value.toArray()))
      return {
        ...state,
        channels: channels
      }
      // break
    case 'CHANNELS': // eslint-disable-line
      // console.log('CHANNELS', action.value)
      localStorage.setItem('channels', JSON.stringify(action.value.toArray()))
      return {
        ...state,
        channels: action.value
      }
      // localStorage.setItem('channels', JSON.stringify(action.value.toArray()))

      // // state.channels = action.value
      // return {
      //   ...state,
      //   channels: action.value
      // }
      // break
    case 'UPDATE_EMOTES':
      // console.log('UPDATE_EMOTES', action, state.emotes)
      state.emotes[action.value.key] = Map(action.value.value)
      return {
        ...state,
        emotes: state.emotes
      }

    case 'UPDATE_BADGES':
      // console.log('UPDATE_BADGES', action, state.badges)
      state.badges[action.value.key] = Map(action.value.value)
      return {
        ...state,
        badges: state.badges
      }
    default:
  }

  // Filter
  switch (action.type) { // TODO scrollbottom
    case 'ADD_FILTER_channel':
      state.filter = state.filter.set('channel', action.value)
      break
    case 'ADD_FILTER_name':
      state.filter = state.filter.set('name', action.value)
      break
    case 'ADD_FILTER_msg':
      state.filter = state.filter.set('msg', action.value)
      break

    default:
  }

  switch (action.type) { // Filter √ºbernehmen
    case 'ADD_FILTER_name':
    case 'ADD_FILTER_channel':
    case 'ADD_FILTER_msg':
    case 'scrollBottom':
    case 'ADD_DATA':
      // TODO: wenn 'state.scrollBottom' === false ist sollte er auch keine Zeilen l√∂schen wenn das 'maxLines' erreicht ist
      // wenn alle Filter === '' sind sollte er wieder nach unten scrollen
      // man sollte die Filter benutzen k√∂nnen auch wenn nicht 'state.scrollBottom' === true ist
      const FILTER = (action.type === 'ADD_FILTER_name' || action.type === 'ADD_FILTER_channel' || action.type === 'ADD_FILTER_msg')
      if (state.scrollBottom || FILTER) {
        let { logs, length, scrollBottom } = Filter(state.allLogs, state.filter)
        const maxLines = Number(state.settings.get('maxLines'))

        if (length > maxLines) { // https://github.com/spddl/react_logein/blob/master/src/store/index.js#L91
          const _10percent = 10 * maxLines / 100
          const _maxLines = maxLines - _10percent

          state.allLogs = state.allLogs.takeLast(_maxLines)
          logs = logs.takeLast(_maxLines)
          length = _maxLines
          refreshCache() // Damit der Index der Zeilen als Key f√ºr den Cache verwendet werden kann
        }

        if (timer) console.timeEnd(action.type)
        return {
          ...state,
          scrollBottom: scrollBottom || state.scrollBottom,
          logs,
          length
        }
      } else {
        if (state.filter.find(value => value !== '') === undefined) { // Wenn kein Filter gesetzt ist
          if (timer) console.timeEnd(action.type)
          return { // nach unten scrollen
            ...state,
            // scrollBottom: true,
            logs: state.allLogs,
            length: state.allLogs.size
          }
        } else {
          if (timer) console.timeEnd(action.type)
          return { ...state } // 'FREEZE' Die Daten werden nicht ge√§ndert
        }
      }
    default:
  }

  // Settings
  switch (action.type) {
    case 'channelColor':
      const newColor = action.value
      action.value = {
        ...state.settings.get(action.type),
        ...newColor
      }
    case 'modalShow': // eslint-disable-line
    case 'twitchBadges':
    case 'bttvBadges':
    case 'twitchEmotes':
    case 'bttvEmotes':
    case 'ffzEmotes':
    case 'fontClass':
    case 'fontSize':
    case 'rowHeight':
    case 'appShow':
    case 'hostnameShow':
    case 'maxLines':
      if (isNaN(!action.value)) action.value = Number(action.value)
      const settings = state.settings.set(action.type, action.value)
      state = {
        ...state,
        settings
      }
      localStorage.setItem('settings', JSON.stringify(settings.toObject()))
      break
    default:
  }

  if (timer) console.timeEnd(action.type)
  if (debug) console.log('Reducer NEWStore', state)
  return state
}

const initialState = {
  allLogs: List(), // https://immutable-js.github.io/immutable-js/docs/#/List
  logs: List(),
  length: 0,
  scrollBottom: true,
  filter: Map({
    name: '',
    level: '',
    hostname: '',
    msg: ''
  }),
  // settings: Map({ // https://immutable-js.github.io/immutable-js/docs/#/Map
  settings: Map(JSON.parse(localStorage.getItem('settings')) || {
    modalShow: false, // true,

    twitchBadges: true,
    bttvBadges: true,

    twitchEmotes: true,
    bttvEmotes: true,
    ffzEmotes: true,

    fontClass: 'inconsolata-font',
    fontSize: 13,
    rowHeight: 19,
    maxLines: 50000,
    channelColor: {}
  }),
  channels: List(JSON.parse(localStorage.getItem('channels')) || []),
  badges: {
    global: Map()
  },
  emotes: {
    global: Map()
  }
}

export const store = createStore(
  LogReducer,
  initialState,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
)
