const generalBoolean = maybeBoolean => maybeBoolean === '1'
// https://github.com/twitch-devs/twitch-js/blob/next/src/Chat/utils/parsers/tags.js

export default (raw) => {
  return raw.split(';')[1].substr(7).split(',').reduce((parsed, badge) => {
    if (badge === '') { return parsed }
    const [rawKey, value] = badge.split('/')
    // return { ...parsed, [rawKey]: value }

    switch (rawKey) {
      // Booleans
      case 'admin':
      case 'broadcaster':
      case 'global-mod':
      case 'moderator':
      case 'partner':
      case 'premium':
      case 'staff':
      case 'sub-gifter':
      case 'turbo':
      case 'vip':
        return { ...parsed, [rawKey]: generalBoolean(value) }
      // Numbers
      case 'bits':
      case 'bits-leader':
      case 'subscriber':
        return { ...parsed, [rawKey]: Number(value) }
      default:
        return { ...parsed, [rawKey]: value }
    }
  }, {})
}
