/* globals fetch:true */

import { connect } from 'react-redux'
import { update } from '../redux'

import difference from 'lodash.difference'

let tempChannels

export const _connect = ({ update, badges, emotes, channels }) => {
  // console.log('badges_connect', badges, channels)

  const StoreChannel = channels.toArray().filter(chan => chan.roomId) // filtere undefined roomId raus

  const ChannelState = tempChannels
  // console.log({ StoreChannel, ChannelState })

  const _join = difference(StoreChannel, ChannelState)
  if (_join.length) {
    // console.log('badges_connectjoin', _join)
  }

  const _part = difference(ChannelState, StoreChannel)
  if (_part.length) {
    // console.log('badges_connectpart', _part)
    // https://badges.twitch.tv/v1/badges/channels/gronkh/display
  }

  if (badges.global.size === 0) { // Badges
    fetch('https://badges.twitch.tv/v1/badges/global/display') // Twitch
      .then(res => {
        if (res.status !== 200) throw new Error(res.status || res.statusText)
        else return res.json()
      })
      .then(res => update({ key: 'global', value: res.badge_sets }, 'UPDATE_BADGES'))
      .catch(err => {
        if (err.message !== '404') {
          console.warn(err.message)
        }
      })
  }

  if (emotes.global.size === 0) { // Emotes
    fetch('https://api.betterttv.net/2/emotes') // BTTV
      .then(res => {
        if (res.status !== 200) throw new Error(res.status || res.statusText)
        else return res.json()
      })
      .then(res => {
        const emotes = {}
        for (let i = 0, len = res.emotes.length; i < len; i++) {
          emotes[res.emotes[i].code] = res.emotes[i]
        }
        update({ key: 'globalbttv', value: emotes }, 'UPDATE_EMOTES')
      })
      .catch(err => {
        if (err.message !== '404') {
          console.warn(err.message)
        }
      })

    fetch('https://api.frankerfacez.com/v1/set/global') // FFZ
      .then(res => {
        if (res.status !== 200) throw new Error(res.status || res.statusText)
        else return res.json()
      })
      .then(res => {
        const resEmotes = res.sets[res.default_sets[0]].emoticons
        const emotes = {}
        for (let i = 0, len = resEmotes.length; i < len; i++) {
          emotes[resEmotes[i].name] = resEmotes[i]
        }
        update({ key: 'globalffz', value: emotes }, 'UPDATE_EMOTES')
      })
      .catch(err => {
        if (err.message !== '404') {
          console.warn(err.message)
        }
      })
  }

  StoreChannel.forEach(value => {
    if (!badges[value.name]) { // Badges
      fetch(`https://badges.twitch.tv/v1/badges/channels/${value.roomId}/display`) // Twitch
        .then(res => {
          if (res.status !== 200) throw new Error(res.status || res.statusText)
          else return res.json()
        })
        .then(res => {
          update({ key: value.name, value: res.badge_sets }, 'UPDATE_BADGES')
        })
        .catch(err => {
          if (err.message !== '404') {
            console.warn(err.message)
          }
        })
    }

    if (!emotes[`${value.name}-bttv`]) { // Emotes
      fetch(`https://api.betterttv.net/2/channels/${value.name}`) // BTTV
        .then(res => {
          if (res.status !== 200) throw new Error(res.status || res.statusText)
          else return res.json()
        })
        .then(res => {
          const emotes = {}
          for (let i = 0, len = res.emotes.length; i < len; i++) {
            emotes[res.emotes[i].code] = res.emotes[i]
          }
          update({ key: `${value.name}-bttv`, value: emotes }, 'UPDATE_EMOTES')
        })
        .catch(err => {
          if (err.message !== '404') {
            console.warn(err.message)
          }
        })
    }

    if (!emotes[`${value.name}-ffz`]) { // Emotes
      fetch(`https://api.frankerfacez.com/v1/room/id/${value.roomId}`) // FFZ
        .then(res => {
          if (res.status !== 200) throw new Error(res.status || res.statusText)
          else return res.json()
        })
        .then(res => {
          if (res.room && res.room.set) {
            if (res.sets[res.room.set] && res.sets[res.room.set].emoticons && res.sets[res.room.set].emoticons.length !== 0) { // das Array k√∂nnte auch leer sein
              const resEmotes = res.sets[res.room.set].emoticons
              const emotes = {}
              for (let i = 0, len = resEmotes.length; i < len; i++) {
                emotes[resEmotes[i].name] = resEmotes[i]
              }
              update({ key: `${value.name}-ffz`, value: emotes }, 'UPDATE_EMOTES')
            }
          }
        })
        .catch(err => {
          if (err.message !== '404') {
            console.warn(err.message)
          }
        })
    }
  })

  tempChannels = StoreChannel
  return null
}

export default connect(
  state => ({
    channels: state.channels,
    badges: state.badges,
    emotes: state.emotes
  }), // mapStateToProps
  { update } // mapDispatchToProps
)(_connect)
